  background-color: #0979ce;
  border-color: #fff;
}

.loader {
  z-index: 505;
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-radius: 50%;
  animation: animloader 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50px;
  left: 50px;
}

@keyframes animloader {
  0% {
    box-shadow: inset -72px 0 #fff;
  }

  100% {
    box-shadow: inset 48px 0 #fff;
  }
}

